import Vue from 'vue'
import Router from 'vue-router'

//懒加载方式，当路由被访问的时候才加载对应组件
const Login = resolve => require(['@/components/Login'], resolve);
const Home = resolve => require(['@/components/Home'], resolve);
const Nofound404 = resolve => require(['@/components/Nofound404'], resolve);
const Forbidden403 = resolve => require(['@/components/Forbidden403'], resolve);
const SysUser = resolve => require(['@/components/system/SysUser'], resolve);
const Role = resolve => require(['@/components/system/Role'], resolve);
const Menu = resolve => require(['@/components/system/Menu'], resolve);
const PersonalCenter = resolve => require(['@/components/system/PersonalCenter'], resolve);
const Setting = resolve => require(['@/components/system/Setting'], resolve);


Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },{
      path: '/login',
      name: 'Login',
      component: Login
    }
    ,{
      path: '/home',
      name: '主页',
      component: Home,
      meta: {
        keepAlive: false,
        requireAuth: true
      },
      children: [
        {
          path: '/sysUser',
          name: '用户管理',
          component: SysUser,
          meta: {
            keepAlive: false,
            requireAuth: true
          }
        },{
          path: '/role',
          name: '权限管理',
          component: Role,
          meta: {
            keepAlive: false,
            requireAuth: true
          }
        },{
          path: '/menu',
          name: '菜单管理',
          component: Menu,
          meta: {
            keepAlive: false,
            requireAuth: true
          }
        },{
          path: '/personalCenter',
          name: '个人中心',
          component: PersonalCenter,
          meta: {
            keepAlive: false,
            requireAuth: true
          }
        }, {
          path: '/setting',
          name: '设置',
          component: Setting,
          meta: {
            keepAlive: false,
            requireAuth: true
          }
        }
      ]
    },
    {
      path: '/forbidden403',
      component: Forbidden403
    },
    {
      path: '*',
      component: Nofound404
    }
  ]
})



